.Carousel {
  display: grid;
  grid-template-columns: 32px 40px 1fr 32px;
  grid-template-areas: ". header header ." "previous slides slides next" ". play-pause indicators .";
  align-items: center;
  gap: 16px 24px;
}
@media (hover: none) {
  .Carousel {
    --control-opacity: 1;
    --indicator-size: 1rem;
  }
}
.Carousel:hover, .Carousel:focus-within {
  --control-opacity: 1;
}
.Carousel[data-expanded=true] {
  --carousel-expand-button-rotate: 180deg;
  --carousel-slides-direction: column;
}
.Carousel__header {
  grid-area: header;
  display: flex;
  align-items: center;
  padding: 16px 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
.Carousel__heading {
  font-size: 1rem;
  font-weight: 600;
  margin: 0 16px 0 0;
}
.Carousel__heading a {
  text-decoration: none;
}
.Carousel__indicators {
  grid-area: indicators;
  display: flex;
}
.Carousel__indicator {
  appearance: none;
  height: var(--indicator-size, 0.75rem);
  width: var(--indicator-size, 0.75rem);
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: var(--indicator-size, 0.75rem);
  border: none;
  opacity: var(--control-opacity, 0);
  transition: background-color 0.15s ease-in-out, opacity 0.15s ease-in-out;
  padding: 0;
  margin-right: 16px;
}
.Carousel__indicator:hover {
  background-color: rgba(0, 0, 0, 0.3);
}
.Carousel__indicator:focus {
  outline: 4px solid #d5e3ff;
}
.Carousel__indicator[aria-expanded=true] {
  background-color: rgba(0, 0, 0, 0.534);
}
.Carousel [data-action=play-pause] {
  grid-area: play-pause;
  opacity: var(--control-opacity, 0);
  transition: opacity 0.15s ease-in-out;
  justify-self: center;
}
.Carousel [data-action=next],
.Carousel [data-action=previous] {
  opacity: 0.45;
  transition: opacity 0.15s ease-in-out;
}
.Carousel [data-action=next]:hover,
.Carousel [data-action=previous]:hover {
  opacity: 1;
}
.Carousel [data-action=next] {
  grid-area: next;
}
.Carousel [data-action=previous] {
  grid-area: previous;
}
.Carousel [data-action=expand-collapse] {
  --button-height: 24px;
  transform: rotateZ(var(--carousel-expand-button-rotate, 0));
}
.Carousel__slides {
  grid-area: slides;
  display: flex;
  flex-direction: var(--carousel-slides-direction, row);
}
.Carousel:not([data-expanded=true]) > .Carousel__slides {
  align-items: center;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
@media (prefers-reduced-motion: no-preference) {
  .Carousel:not([data-expanded=true]) > .Carousel__slides {
    scroll-behavior: smooth;
  }
}
.Carousel:not([data-expanded=true]) > .Carousel__slides::-webkit-scrollbar {
  display: none;
}
.Carousel__slide {
  flex: 1 0 100%;
  scroll-snap-align: start;
  scroll-snap-stop: always;
}